function permute(arr){
    return permuteArr(arr.split(''))
}

function permuteArr(arr){
    switch(arr.length){
        case 0:
            return []
        case 1:
            return arr
        default: return flatten(
            permuteArr(without(arr, a)).map((b)=>{
                return a.concat(b);
            })
        )
    }
}

function without(arr, a){
    let result = [...arr]
    result.splice(a.indexOf(a), 1)
    return result
}

function flatten(arr){
    return arr.reduce((acc, curr)=> acc.concat(curr), [])
}

// tests
import { test } from 'ava'

test(t => t.deepEqual(permute(''), []))
test(t => t.deepEqual(permute('abc'), ['abc', 'acb', 'bac', 'bca', 'cab', 'cba']))